.mainacc{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.mainacc h1{
    font-size: 4vw;
    color: #024F9D;
    margin-top: 2vw;
}
.mainacc img {
    width: 15vw;
}
.mainacc input{
    width: 50%;
    border: none;
    border-bottom: solid 2px #D9D9D9;
    text-align: center;
    font-size: 3vw;
}
.updateacc {
    display: flex;
    justify-content: center;
    width: 50%;
    height: 10vw;
    background-color: #D9D9D9;
    border-radius: 3vw;
    border: none;
    margin-top: 2vw;
}

.updateacc img {
    width: 10vw;
}
.mainsecond h1 {
    font-size: 4vw;
    color: #024F9D;
    margin-top: 5vw;
    margin-left: 32vw;
}
.cardacc{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border: 2px solid #61B846;
    border-radius: 3vw;
    margin: 3vw 30vw;
    padding: 1vw 3vw;
}
.cardacc .fchild img{
    width: 8vw;
}
.cardacc .schild h1{
    color: #61B846;
    font-size: 2vw;
    margin-left: 3vw;
}
.fixcat{
    height: 30vw;
    overflow-y: scroll;
}
.Logout{
    border: none;
    background-color: #61B846;
    border-radius: 2rem;
    color: white;
    padding: 0.2vw 4vw;
    margin: 4vw 0vw 6vw 40vw;
    font-size: 3vw;
    font-weight: 500;
}