.cardall{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 2px solid #61B846;
    border-radius: 3vw;
    margin: 3vw 18vw;
    padding: 1vw 2vw;
}
.fchild img {
    width: 12vw;
}
.schild h1{
    color: #61B846;
    font-size: 3vw;
}
.schild{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.schild p {
    color: #D9D9D9;
    font-size: 2vw;
}
.tchild p{
    font-size: 2vw;
    color: #D9D9D9;
}
.h1{
    font-size: 2.7vw;
    color: #024F9D;
    margin-left: 9vw;
    margin-top: 2vw;
}