.mainadd {
    height: 80%;
}

.h1 {
    font-size: 2.7vw;
    color: #024F9D;
    margin-left: 9vw;
    margin-top: 2vw;
}

.addinputs {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.addinputs input {
    width: 50%;
    padding: 2vw 3vw;
    margin: 0.5vw 3vw;
    border: none;
    border-radius: 3vw;
    background-color: #D9D9D9;
    font-size: 2vw;
}

#desc {
    margin-top: 0.8vw;
    padding: 2vw 3vw 10vw 3vw;
    background-color: #D9D9D9;
    border-radius: 3vw;
    border: none;
    text-align: left;
}

.update {
    display: flex;
    justify-content: center;
    width: 50%;
    height: 20vw;
    background-color: #D9D9D9;
    border-radius: 3vw;
    border: none;
}

.update img {
    width: 20vw;

}

.unitname {
    display: flex;
    justify-content: space-evenly;
    margin: 2vw 0;
}

.unitname input {
    border-radius: 3vw;
    border: none;
    background-color: #D9D9D9;
    width: 30vw;
    padding: 0 1vw;
    font-size: 2vw;
}

.unitname h1 {
    font-size: 3vw;
    color: #024F9D;

}

.unitprice {
    display: flex;
    justify-content: space-evenly;
    margin: 2vw 0;
}

.unitprice input {
    border-radius: 3vw;
    border: none;
    background-color: #D9D9D9;
    width: 30vw;
    margin-left: 1vw;
    padding: 0 1vw;
    font-size: 2vw;
}

.unitprice h1 {
    font-size: 3vw;
    color: #024F9D;
}

.addbtn {
    border: none;
    background-color: #61B846;
    border-radius: 2rem;
    color: white;
    padding: 0.2vw 4vw;
    margin: 4vw 0vw 6vw 35vw;
    font-size: 3vw;
    font-weight: 500;
}