.nav{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.4vw 6vw;
    border-bottom: 1px solid #D9D9D9;
}
.imgandtext{
    display: flex;
}
.text h1{
    font-size: 3vw;
    color: #024F9D;
}
.text h2{
    font-size: 2vw;
    color: #61B846;
}
.imgandtext img {
    width: 7vw;
    margin-right: 2vw;
}
.menu img {
    width: 4vw;
}